import React from 'react';
import '../App.css';
import Footer from './Footer';
import AddItem from './AddItem';
import VisibleItemList from './VisibleItemList';

const App = () => (
  <div className="App">
    <header className="App-header">
      <h1 className="App-title">Welcome To My Grocery List App</h1>
      <p>*Create shopping list by adding new items.</p>
      <p>*Click on items after purchased to strike-out.</p>
      <p>*Click on striked-out items to delete from list.</p>
    </header>
    <AddItem />
    <Footer />
    <VisibleItemList />
    <footer className="App-footer">
      <h1 className="App-code">
        <a target="_blank"
          rel="noopener noreferrer"
          href="https://github.com/jffernan/my-grocery-list-app">
          Visit GitHub To View My Code
        </a>
      </h1>
    </footer>
  </div>
);

export default App;



